<template lang="Python">
<el-col class="case-detail">
  <el-row :gutter="20">
    <el-col :span="12">
      <el-card>
        <el-table :data="[caseDetail.device.system_info]" style="width: 100%" stripe border>
          <el-table-column label="执行环境" align="center">
            <el-table-column prop="model" label="设备"></el-table-column>
            <el-table-column prop="platform" label="平台"></el-table-column>
            <el-table-column prop="system" label="系统"></el-table-column>
            <el-table-column prop="SDKVersion" label="基础库"></el-table-column>
          </el-table-column>
        </el-table>

        <el-table :data="[caseDetail]" style="width: 100%" stripe border>
          <el-table-column label="运行信息" align="center">
            <el-table-column prop="case_name" label="名称"></el-table-column>
            <el-table-column prop="module" label="包名"></el-table-column>
            <el-table-column prop="costs" label="耗时"></el-table-column>
            <el-table-column label="结果">
              <template slot-scope="scope">
                {{statusMsg(scope.row)}}
              </template>
            </el-table-column>
          </el-table-column>
        </el-table>

        <el-table :data="[caseDetail]" style="width: 100%" stripe border>
          <el-table-column prop="relative" label="用例产物存储路径" align="center">
            <!-- <el-table-column prop="relative" label="用例产物存储路径" align="center"></el-table-column> -->
          </el-table-column>
        </el-table>
      </el-card>
    </el-col>
    <el-col :span="10" style="margin-left:1em">
      <el-card>
        <div slot="header" class="clearfix">
          <span>用例说明</span>
        </div>
        <pre>{{ caseDetail.case_doc }}</pre>
      </el-card>
    </el-col>
  </el-row>

  <el-collapse :value="['performance']" >
    <!-- 性能图表需要显示才能画，默认打开性能标签  -->
    <el-collapse-item title="运行截图" name="capture">
      <el-col v-if="screen_info && screen_info.length>0" style="overflow: auto;">
        <el-row v-for="(screen_item, idx) in screen_info" :key="idx" type="flex">
          <el-col v-for="(screen, index) in screen_item" :key="index" :span="4">
            <el-image :src="prefix+'/'+screen.path" :preview-src-list="screen.preview_src_list" style="width:95%">
            </el-image>
            <div style="padding:14px">
              <span>{{ screen.name }}</span>
              <div class="bottom clearfix">
                <time class="time">{{ screen.datetime }}</time>
              </div>
            </div>
          </el-col>
        </el-row>
      </el-col>
      <el-col v-else>
        <div style="color: #aaa; font-weight: bold;">无截图</div>
      </el-col>
    </el-collapse-item>
    <el-collapse-item title="代码和堆栈" name="code">
      <el-row v-if="codeShow" :gutter="20">
        <el-col :span="12">
          <h3>代码</h3>
          <div v-if="caseDetail.error_extra_info && caseDetail.error_extra_info !== ''">
            <div v-for="(line, index) in caseDetail.source.code" :key="index">
              <pre style="color:red" v-if="caseDetail.failed_line_num === caseDetail.source.start + index">{{ line }}</pre>
              <pre style="color: blue" v-else-if="caseDetail.error_extra_info.start <= index && caseDetail.error_extra_info.end > index">{{ line }}</pre>
              <pre data-lang="Python" v-else><code>{{ line }}</code></pre>
            </div>
          </div>
          <div v-else>
            <div v-for="(line, index) in caseDetail.source.code" :key="index">
              <pre style="color:red" v-if="caseDetail.failed_line_num === caseDetail.source.start + index">{{ line }}</pre>
              <pre data-lang="Python" v-else><code>{{ line }}</code></pre>
            </div>
          </div>
        </el-col>
        <el-col :span="12">
          <h3>错误堆栈</h3>
          <div v-if="caseDetail.errors||caseDetail.failures">
            <pre style="color:red">{{ caseDetail.errors||caseDetail.failures }}</pre>
          </div>
        </el-col>
      </el-row>
    </el-collapse-item>
    <el-collapse-item title="运行日志" name="log">
      <div class="log-box">
        <pre v-for="(log, index) in logs" :key="index" :class="getLogClass(log)">{{ log }}</pre>
      </div>
    </el-collapse-item>
    <el-collapse-item title="小程序日志" name="weapp-log">
      <div class="log-box" v-if="appLogs.length>0">
        <pre v-for="(log, index) in appLogs" :key="index" :class="getLogClass(JSON.stringify(log))">{{"["+log.type+"] - "+JSON.stringify(log.args)+"\n\n" }}</pre>
      </div>
      <div v-else>
        <div style="color: #aaa; font-weight: bold;">无日志</div>
      </div>
    </el-collapse-item>
    <el-collapse-item title="网络请求日志" name="request-log">
      <RequestLog :requestLogs="requestLogs" />
    </el-collapse-item>
    <el-collapse-item v-if="caseDetail.performance||caseDetail.fps" title="性能信息" name="performance">
      <ve-line v-if="caseDetail.performance" :toolbox="toolbox" :data="perfData" :settings="chartSettings"></ve-line>
      <ve-line v-if="caseDetail.fps" :grid="grid" :toolbox="toolbox" :data="fpsData" :mark-line="markLine"></ve-line>
    </el-collapse-item>
  </el-collapse>
</el-col>
</template>

<script>
import VeLine from 'v-charts/lib/line.common'
import 'echarts/lib/component/markLine'
import JsonViewer from 'vue-json-viewer'
import RequestLog from './RequestLog'
const axios = require('axios')
var dateFormat = require('dateformat')
// Vue.use(JsonViewer)

export default {
  name: 'CaseDetail',
  components: {
    VeLine,
    JsonViewer,
    RequestLog
  },
  props: {
    caseDetail: Object
  },
  data () {
    this.chartSettings = {
      xAxisType: 'time'
    }
    this.grid = {
      right: '10%'
    }
    this.toolbox = {
      feature: {
        magicType: { type: ['line', 'bar'] },
        saveAsImage: {}
      }
    }
    this.markLine = {
      data: [
        {
          name: '平均值',
          label: {
            formatter: '{b}: {c}'
          },
          type: 'average'
        },
        {
          name: '最大值',
          label: {
            formatter: '{b}: {c}'
          },
          type: 'max'
        },
        {
          name: '最小值',
          label: {
            formatter: '{b}: {c}'
          },
          type: 'min'
        }
      ]
    }
    return {
      codeShow: true,
      showLog: false,
      logs: [],
      appLogs: [],
      requestLogs: [],
      perfData: {
        columns: ['time', 'CPU', 'MEM'],
        rows: []
      },
      fpsData: {
        columns: ['second', '帧数'],
        rows: []
      },
      screen_info: []
    }
  },
  methods: {
    refresh () {
      console.warn('refresh')
      let screenInfo = []
      let screenItem = []
      for (let i = 0; i < this.caseDetail.screen_info.length; i++) {
        if (i % 4 === 0) {
          if (screenItem.length) screenInfo.push(screenItem)
          screenItem = []
        }
        screenItem.push(this.caseDetail.screen_info[i])
      }
      if (screenItem.length) screenInfo.push(screenItem)
      this.screen_info = screenInfo

      let perfData = {
        columns: ['time', 'CPU', 'MEM'],
        rows: []
      }
      if (this.caseDetail.performance) {
        for (let item of this.caseDetail.performance) {
          let timestamp = Object.keys(item)[0]
          perfData.rows.push({
            time: dateFormat(new Date(parseFloat(timestamp) * 1000), 'yyyy/mm/dd HH:MM:ss'),
            CPU: item[timestamp].cpu,
            MEM: item[timestamp].mem
          })
        }
      }
      this.perfData = perfData

      let fpsData = {
        columns: ['second', '帧数'],
        rows: []
      }
      if (this.caseDetail.fps) {
        for (let key in this.caseDetail.fps.fpsvalue) {
          fpsData.rows.push({
            second: key,
            '帧数': this.caseDetail.fps.fpsvalue[key]
          })
        }
      }
      this.fpsData = fpsData

      axios
        .get(`${this.prefix}/${this.caseDetail.log_filename}`)
        .then(resp => {
          this.logs = resp.data.split('\n')
          // this.$set("logs", resp.data.split("\n"))
        })
        .catch(err => {
          console.log(err)
        })

      console.warn(`${this.prefix}/${this.caseDetail.weapp_log_path}`)
      axios
        .get(`${this.prefix}/${this.caseDetail.weapp_log_path}`)
        .then(resp => {
          this.appLogs = resp.data.split('\n')
          for (let index = 0; index < this.appLogs.length; index++) {
            if (this.appLogs[index] === '') {
              this.appLogs.pop(index)
              continue
            }
            this.appLogs[index] = JSON.parse(this.appLogs[index])
          }
        })
        .catch(err => {
          console.log(err)
        })

      console.warn(`${this.prefix}/${this.caseDetail.request_log_path}`)
      axios
        .get(`${this.prefix}/${this.caseDetail.request_log_path}`)
        .then(resp => {
          this.requestLogs = resp.data.split('\n')
          for (let index = 0; index < this.requestLogs.length; index++) {
            if (this.requestLogs[index] === '') {
              this.requestLogs.pop(index)
              continue
            }
            this.requestLogs[index] = JSON.parse(this.requestLogs[index])
          }
          // this.$set("logs", resp.data.split("\n"))
        })
        .catch(err => {
          console.log(err)
        })
    },
    getLogClass (log) {
      // console.log(log)
      if (log.includes('NATIV') || log.includes('DEBUG') || log.includes('METHO') || log.includes('"debug"')) {
        return 'log-debug'
      } else if (log.includes('INFO') || log.includes('"log"') || log.includes('"info"')) {
        return 'log-info'
      } else if (log.includes('WARN') || log.includes('"warn"')) {
        return 'log-warning'
      } else {
        return 'log-error'
      }
    },
    statusMsg (caseDetail) {
      if (caseDetail.success) {
        return '成功'
      }
      if (caseDetail.is_error) {
        return '异常'
      }
      if (caseDetail.is_failure) {
        return '失败'
      }
    }
  },

  mounted () {
    this.refresh()
  },
  computed: {
    prefix: function () {
      return `${process.env.BASE_URL}${this.caseDetail.relative}`
    }
  },
  watch: {
    caseDetail () {
      this.refresh()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped>
pre {
  margin: 0;
  /* overflow: auto;
  overflow-y:hidden； */
  white-space: pre-wrap;
  word-wrap: break-word;
}

.log-box {
  padding: 1em 0 0 0.5em;
  background-color: #3c3f41;
}
.white-log-box {
  padding: 1em 0 0 0.5em;
  background-color: #ffffff;
}

.log-debug {
  color: #c1c6cf;
}

.log-info {
  color: #67c23a;
}

.log-warning {
  color: #e6a23c;
}

.log-error {
  color: #f56c6c;
}
</style>
