<template lang="Python">
  <div class="white-log-box" v-if="requestLogs.length>0">
    <el-collapse :value="activeName" accordion @change="handleChange" name="request-log-item">
      <el-collapse-item v-for="(log, index) in requestLogs" :key="index" :name="index">
        <template slot="title">
          <el-descriptions :colon="false" :column="5">
            <el-descriptions-item>
              {{dateFormat(log.start_timestamp || parseInt((log.timestamp * 1000)))}}
            </el-descriptions-item>
            <el-descriptions-item :span="3">
              {{log.request.url}}
            </el-descriptions-item>
            <el-descriptions-item :contentStyle="{'justify-content': 'end'}">
              <el-tag v-if="log.mocked" type="success">mocked</el-tag>
              <el-tag v-if="log.response && log.response.statusCode" :type="getStatus(log.response.statusCode)">{{log.response.statusCode}}</el-tag>
            </el-descriptions-item>
          </el-descriptions>
        </template>
        <el-container>
          <el-header>请求</el-header>
          <el-main v-if="log.request"><JsonViewer :value="log.request"/></el-main>
        </el-container>
        <el-container>
          <el-header>响应</el-header>
          <el-main v-if="log.response"><JsonViewer :value="log.response"/></el-main>
        </el-container>
      </el-collapse-item>
    </el-collapse>
  </div>
  <div v-else>
    <div style="color: #aaa; font-weight: bold;">无网络请求日志</div>
  </div>
</template>

<script>
import JsonViewer from 'vue-json-viewer'
var dateFormat = require('dateformat')
// Vue.use(JsonViewer)

export default {
  name: 'RequestLog',
  components: {
    JsonViewer
  },
  props: {
    requestLogs: Array
  },
  data () {
    return {
      activeName: '0'
    }
  },
  methods: {
    handleChange (val) {
      console.log(val)
    },
    dateFormat (date) {
      return dateFormat(date, 'yyyy/mm/dd HH:MM:ss')
    },
    getStatus (code) {
      return code === 200 ? 'success' : 'fail'
    }
  }
}
</script>

